@import 'ScssHelpers/helpers-all.scss';

.copy_link {
	position: relative;

	font-size: 2.4rem;
	font-weight: 700;
	font-style: italic;
	line-height: 1.2;
	color: #b6ba4b;
	text-transform: uppercase;

	span {
		position: absolute;
		opacity: 0;
		right: -3.2rem;
		top: 2.7rem;
		height: 2.4rem;
		padding: 0 0.6rem;

		background-color: #343434;
		color: $white;
		line-height: 2.4rem;
		font-size: 1.2rem;
		font-style: normal;
		font-weight: 300;
		text-transform: none;
		border-radius: 3px;

		pointer-events: none;
		transition: opacity 0.3s ease;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: #afb523;
	}

	&--copied {
		span {
			opacity: 1;
		}
	}
}
