@import 'ScssHelpers/helpers-all.scss';

@mixin flex-center {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.article_content {
	font-family: $raleway;
	color: $black_3;

	background-color: #f8f8f8;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		background-color: $black_2;

		@include bp($point_2, min) {
			height: 8rem;
		}

		@include bp($point_3) {
			height: 6.9rem;
		}
	}

	@include bp($point_2, min) {
		padding-top: 8rem;
		padding-bottom: 6.3rem;
	}

	@include bp($point_3) {
		padding-bottom: 6.9rem;
		padding-top: 6.9rem;
	}

	p {
		font-size: 1.9rem;
		font-weight: 500;
		line-height: 1.16;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 4rem;
			}
		}
	}

	h1 {
		font-size: 5.2rem;
	}

	h2 {
		font-size: 4.6rem;
	}
	h3 {
		font-size: 3.8rem;
	}
	h4 {
		font-size: 3.2rem;
	}
	h5 {
		font-size: 2.6rem;
	}
	h6 {
		font-size: 2rem;
	}

	.wp-block-image {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}

		img {
			max-width: 100%;
			width: 100%;
			height: auto;
			display: block;
		}
	}

	.section_in {
		@include bp($point_2, min) {
			max-width: 55.9rem;
		}
	}
}

.article_content__body {
	ul:not([class^='wp-block']),
	ol {
		padding-left: 2rem;

		li {
			font-size: 1.9rem;
			font-weight: 500;
			line-height: 1.16;

			&:not(:last-child) {
				@include bp($point_2, min) {
					margin-bottom: 1.6rem;
				}

				@include bp($point_3) {
					margin-bottom: 1.2rem;
				}
			}
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 4rem;
			}
		}
	}

	ul:not(.wp-block-social-links) li {
		list-style-type: disc;
	}

	ol li {
		list-style-type: decimal;
	}
}

.article_back {
	background-color: $black_2;

	@include bp($point_2, min) {
		padding: 2.6rem 0;
		margin: 0 calc(var(--gl-indent) * -1) 5.2rem;
	}

	@include bp($point_3) {
		padding: 2.6rem var(--gl-indent) 3.8rem;
		margin: 0 calc(var(--gl-indent) * -1) 3.6rem;
	}
}

.article_back__link {
	display: flex;
	align-items: center;
	margin-left: auto;
	margin-right: auto;

	color: $white;
	font-size: 1.9rem;
	font-weight: 500;
	font-family: $raleway;

	transition: color 0.3s ease;

	@include bp($point_1 + 1, min) {
		max-width: 113.8rem;
	}

	@include bp($point_1, $point_2) {
		max-width: 100%;
		padding: 0 var(--gl-indent);
	}

	&:hover,
	&:focus {
		color: $yellow;
		text-decoration: none;
	}
}

.article_back__icon {
	width: 2.4rem;
	height: 2.4rem;
	margin-right: 0.3rem;

	.icon {
		transition: fill 0.3s ease;
	}
}

.article_content__head {
	position: relative;

	@include bp($point_2, min) {
		margin-bottom: 3.5rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}

	&:before {
		content: '';
		position: absolute;
		left: -2.5rem;
		bottom: -3.5rem;
		width: 1.7rem;
		height: 1.7rem;

		background-color: $black_2;
		border-radius: 100%;
	}
}

.article_content__title {
	margin-bottom: 1rem;

	font-weight: 700;
	font-style: italic;
	text-transform: uppercase;
	letter-spacing: -0.02em;
	font-size: 3.2rem;
	line-height: 1.2;
}

.article_content__excerpt {
	margin-bottom: 0.5rem;
	padding-bottom: 1.2rem;

	font-weight: 500;
	font-size: 1.9rem;
	border-bottom: 3px solid $black_2;
}

.article_content__meta {
	display: flex;
	justify-content: space-between;
	align-items: center;

	font-size: 1.6rem;
	color: #909090;
	font-weight: 500;
}

.article_content__body {
	@include bp($point_2, min) {
		padding-bottom: 7.7rem;
	}

	@include bp($point_3) {
		padding-bottom: 6.1rem;
	}
}

.article_content__bottom {
	@include flex-center();

	padding-top: 1.8rem;

	border-top: 3px solid $black;

	@include bp($point_3) {
		align-items: flex-start;
	}
}

.article_social {
	@include flex-center();

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

.article_social__list {
	@include flex-center();
}

.article_social__title {
	font-size: 2.4rem;
	font-weight: 700;
	font-style: italic;
	text-transform: uppercase;
	letter-spacing: -0.02em;

	@include bp($point_2, min) {
		margin-right: 1.4rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 1rem;
	}
}

.article_sosial__item {
	width: 2.4rem;
	height: 2.4rem;

	&:not(:last-child) {
		margin-right: 0.8rem;
	}
}

.article_social__link {
	color: #343434;

	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: $yellow;
	}
}
